import { Input } from '@angular/core';
import { ConfigurationService } from '../../common/configuration.service';
import { SettingsComponent } from '../../common/settings.component';
import { Border } from '../../common/property-types';

/**
 * @hidden
 */
export abstract class CategoryAxisNotesIconComponentGenerated extends SettingsComponent {
  /**
   * The background color of the notes icon.
   */
  @Input() public background: string;

  /**
   * The border of the icon.
   */
  @Input() public border: Border;

  /**
   * The size of the icon.
   */
  @Input() public size: number;

  /**
   * The icon shape.
   *
   * The supported values are:
   * * `"circle"`&mdash;The marker shape is a circle.
   * * `"square"`&mdash;The marker shape is a square.
   * * `"triangle"`&mdash;The marker shape is a triangle.
   * * `"cross"`&mdash;The marker shape is a cross.
   */
  @Input() public type: string;

  /**
   * The icon visibility.
   */
  @Input() public visible: boolean;

  constructor(public configurationService: ConfigurationService) {
    super('notes.icon', configurationService);
  }
}



